@import './vars.scss';

*, *:before, *:after, graph-2-data {
    box-sizing: border-box;
    font-family: 'fira code retina', 'Lucida Console', Monaco, monospace;
    font-size: 10pt;
}

*:focus {
    outline: none;
}

.clearfix::after {
    display: block;
    clear: both;
    content: "";
}

html, body, .App, .api-driver, .main-ui-container {
    width: 100%;
    height: 100%;
}
html {
    overflow: hidden;
}

*[title] {
    cursor: help;
}

.App {
    text-align: center;
    width: 100%;
    height: 100%;
}

.App-logo {
    animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
    pointer-events: none;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

.api-driver, .main-ui-container {
    border: 1px solid red;
    padding: 0;
    display: flex;
}

.api-driver fieldset {
    border: 1px solid red;
    width: auto;
    display: inline-block;
    flex: 0 0;
    margin-bottom: 0.5em;
}

.api-driver fieldset legend {
    font-size: 0.8em;
    color: red;
}

.api-driver .state-display {
    border: 1px solid red;
    flex: 1 1;
    text-align: left;
    padding: 1em;
    overflow: auto;
    margin: 0 0.5em 0 0;
}

.api-driver .state-editors {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    overflow: auto;
    margin: 0 0 0 0.5em;
    border: 1px solid red;
    padding: 1em;
}

.api-driver .state-editors .coord {
    width: 40px;
    margin-right: 20px;
    margin-left: 3px;
}

.api-driver .state-editors .name {
    width: 80px;
    margin-right: 20px;
    margin-left: 3px;
}

.api-driver .state-editors button {
    margin-right: 10px;
}

.api-driver fieldset ul {
    list-style: none;
    padding-left: 0;
    text-align: left;
}

label, button, option, input[type=checkbox] {
    cursor: pointer;
}

button[disabled] {
    cursor: not-allowed;
}

.main-ui-container {
    flex-direction: column;
    border-width: 0;
}

.main-ui-container .middle {
    width: 100%;
    height: 100%;
}

.main-ui-container .middle.column .image-stage {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: auto;
}

.main-ui-container .middle.column .image-stage canvas.image, .main-ui-container .middle.column .image-stage canvas.chrome {
    position: absolute;
    left: 0;
    top: 0;
    cursor: crosshair;
}

.main-ui-container .right.column {
    display: flex;
    flex-direction: column;
}

.main-ui-container .right.column .magnifier-stage {
    box-sizing: content-box;
    display: flex;
}

.main-ui-container .magnifier-stage {
    border: 1px solid $primaryBackgroundHilight;
    position: relative;
}

.main-ui-container .magnifier-stage canvas {
    position: absolute;
    left: 0;
    width: 100%;
    height: 100%;
}

.main-ui-container .magnifier-stage .image {
    z-index: 1;
}

.main-ui-container .magnifier-stage .image-chrome {
    z-index: 2;
}

.main-ui-container .magnifier-stage .chrome {
    z-index: 3;
}

.main-ui-container .magnifier-wrapper {
    display: flex;
    flex-direction: column;
}

.main-ui-container .top {
    background-color: $primaryBackground;
    height: 50px;
    border-bottom: 1px solid $primaryBackgroundHilight;
}

.main-ui-container .middle {
    background-color: $primaryBackground;
    flex: 1 0;
    display: flex;
    border-left: 1px solid $primaryBackgroundHilight;
}

.main-ui-container .bottom {
    background-color: $primaryBackground;
    height: 50px;
    border-top: 1px solid $primaryBackgroundHilight;
}

.main-ui-container .left {
    background-color: $primaryBackground;
    width: 50px;
}

.main-ui-container .middle {
    background-color: $primaryBackground;
    flex: 1 1;
}

.main-ui-container .middle.bar .right.column {
    background-color: $primaryBackground;
    border-left: 1px solid $primaryBackgroundHilight;
    display: flex;
    flex-direction: column;
}

.duxpanel-backdrop {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, .3);
}

.duxpanel {
    border: 1px solid #ddd;
    background-color: #fff;
    border-radius: 4px;
    position: fixed;
    box-shadow: rgba(0, 0, 0, 0.6) 0px 0px 6pt 3pt;
}

.duxpanel-heading {
    background-color: $primaryBackgroundHilight;
    color: white;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
    padding: 10px 15px;
    cursor: default;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    cursor: move;
}

.duxpanel-body {
    display: flex;
}

.wizard {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.wizard .body {
    display: flex;
    flex-direction: column;
    padding: 0;
    justify-content: stretch;
    flex: 1;
    width: 100%;
}

.wizard .body select {
    width: 100%;
    height: 100%;
    border: 0px solid red;
    padding: 0.5em;
}

.wizard .body select.single-line {
    padding: 0.25em;
}

.wizard .body select option {
    margin: 0.2em;
}

.wizard .body .row {
    padding: 0.25em;
}

.wizard .body .row label input {
    margin-left: 0.25em;
}

.wizard .body .row input[name='data-series-count'], .wizard .body .row input[name='data-points-count'] {
    width: 40px
}

.wizard .footer {
    border-top: 1px solid $primaryBackgroundHilightDark3;
    padding: 0.5em;
    background-color: $primaryBackgroundHilight;
    color: white;
}

.wizard .cta-footer {
    display: none;
    border-top: 1px solid $primaryBackgroundHilightDark2;
    padding: 0.5em;
    background-color: $primaryBackgroundHilight;
    color: white;
    font-size: 8pt;
}

.duxpanel {
    border: 1px solid $primaryBackgroundHilight;
}

.duxpanel-footer .dialog-button {
    margin: 0 0 0 0.5em !important;
}

.status-display {
    margin-top: 0.5em;
    margin-bottom: 1em;
}

.axis-calibration {
    margin: 1em;
}

.status-display .body * {
    font-size: 8pt;
}

.status-display {
    margin-top: 0;
}

.status-display .header, .axis-calibration .header {
    background-color: $primaryBackgroundHilight;
    color: white;
    margin-bottom: 0.5em;
}

.status-display {
    flex: 0 !important;
}

.axis-calibration {
    flex: 1;
}

.zoom-controls {
    display: flex;
    justify-content: space-between;
    margin: 0.25em;
    border: 1px solid $primaryBackgroundHilight;
    border-radius: 5px;
    padding: 0.25em;
    color: $primaryBackgroundHilight;
}


.zoom-controls>label {
    margin: 0 0.5em;
}

.zoom-controls .current-value {
    margin: 0 0.25em;
}

.toggle-icon {
    display: flex;
    justify-content: space-between;
}

.toggle-icon>label {
    margin: 0 0.5em
}

.toggle-icon.on {
    color: $primaryBackgroundHilightDark3;
}

.toggle-icon.on svg {
    font-size: 2.2rem;
}

.axis-diagram-container {
    display: flex;
    border-bottom: 2px solid $primaryBackgroundHilight;
    padding: 0.5em !important;
}

.axis-diagram {
    display: block;
    border: 2px solid $primaryBackgroundHilight;
    margin-right: 0.5em;
    padding: 0.5em;
}

.axis-diagram:last-child {
    margin-right: 0;
}

.axis-diagram>label {
    margin-top: 0.3em;
    display: block;
}

.axis-calibration-value {
    display: none;
}
.axis-log-setting, .axis-log-power-setting {
    display: none;
}
.combined-origin {
    fill: palevioletred !important;
    font-size: 2em !important;
}
.combined-origin.on {

}
.combined-origin.off {

}
/* *:focus {
    outline: 2px solid red;
} */
select:focus, button:focus {
    outline-width: 0;
}
.axis-calibration-value:focus {
    outline-width: 0;
}
.axis-log-setting > input:focus, .axis-log-power-setting > input:focus {
    outline-width: 0;
}
.axis-log-setting:focus-within, .axis-log-power-setting:focus-within {
    outline: 1px dotted red;
}

.axis-calibration .cta {
    color: white;
    background-color: palevioletred;
    border: 1px solid palevioletred;
    border-radius: 20px;
    padding: 0 0.7em;
}

.axis-calibration .cta:before {
    content: '>';
    font-size: 20pt;
    vertical-align: -0.15em;
    margin-right: 0.15em;
    font-weight: bold;
    color: white;
}

.matrix-display {
    border: 1px solid $primaryBackgroundHilight;
    padding: 0;
    display: flex;
    flex-direction: column;
}

.matrix-display>.matrix-row {
    padding: 0;
    margin: 0;
    display: flex;
}

.matrix-display>.matrix-row>.matrix-element {
    border: 1px solid $primaryBackgroundHilight;
    padding: 0.25em;
    margin: 0;
    flex: 1 0;
}

.data-points-table-container {
    max-height: 250px;
    overflow-y: auto;
}

.data-points-table {
    border-collapse: collapse;
    width: 100%;
}

.data-points-table thead, .data-points-table tr, .data-points-table tbody, .data-points-table th, .data-points-table td {
    font-size: inherit;
}
.data-points-table td, .data-points-table th {
    border: 1px solid $primaryBackgroundHilight;
}

.data-points-table td.selected {
    background-color: $primaryBackgroundHilightLight3;
}

.data-points-table td.data-cell, .data-points-table td.blank-cell {
    cursor: pointer;
}

.data-points-table td.auto-add {
    background-color: $primaryBackgroundHilightLight3;
}

.top.bar {
    display: flex;
    flex-direction: row-reverse;
}

.settings {
    border: 1px solid var (--primaryBackgroundHilightDark3);
    background-color: $primaryBackground;
    color: $primaryBackgroundHilight;
    display: flex;
    align-self: center;
    align-items: center;
}

.series-selector-panel .button-bar,
.data-points-meta-table-container .button-bar {
    display: flex;
    align-content: stretch;
}

.series-selector-panel .button-bar button,
.data-points-meta-table-container .button-bar button {
    flex: 1;
}

.series-selector-panel .button-bar button svg,
.data-points-meta-table-container .button-bar button svg {
    font-size: 1.4rem;
}

.data-points-table svg {
    font-size: 1.3rem;
}

.data-points-table svg.disabled {
    color: #ccc;
}

::-webkit-input-placeholder {
    color: $placeholderTextColor;
}

::-moz-placeholder {
    color: $placeholderTextColor;
}

:-ms-input-placeholder {
    color: $placeholderTextColor;
}

:-moz-placeholder {
    color: $placeholderTextColor;
}

.base {
    vertical-align: sub;
    font-size: 0.7em;
}

.pow {
    vertical-align: super;
    font-size: 0.7em;
}
.disabled {
    color: #ccc;
}