.graph-displayer-api-driver {
    margin: 1em 1em 0 1em;
    padding: 0.5em;
    border: 1px solid red;
    cursor: pointer;
}

.graph-coords {
    font-size: 0.8em;
    border-top: 1px dotted red;
    margin-top: 1em;
    padding-top: 0.25em;
    display: block;
    margin-left: -0.5em;
    margin-right: -0.5em;
    padding: 0.5em 0.5em 0 0.5em;
    text-align: center;
}